
.b-color-f {
    background: #fff;
    padding-bottom: 40px;
    /*margin: 10px 0px;*/
}
.filter-top {
    width: 4.6rem;
    height: 4.6rem;
    text-align: center;
    line-height: 4.6rem;
    background: rgba(0, 0, 0, 0.5);
    border-radius: 100%;
    bottom: 12rem;
    right: 1.6rem;
    left: inherit;
}
.filter-top {
    display: none;
}
.filter-top .icon-jiantou {
    position: absolute;
    left: 0;
    right: 0;
    font-size: 2rem;
    color: #fff;
    transform: rotate(90deg);
}
.search-div {
    background: #fff;
    position: fixed;
    height: 100%;
    width: 100%;
    left: 0;
    top: 100%;
    right: 0;
    bottom: 0;
    z-index: 112;
    visibility: hidden;
}
.ts-3 {
    transition: all .3s;
}
.close-search {
    height: 4.6rem;
    line-height: 4.6rem;
    color: #777;
    position: absolute;
    bottom: 0;
    font-size: 1.7rem;
    text-align: center;
    width: 100%;
}
.user-center {
    margin-top: 2rem;
    font-size: 1.6rem;
}
.margin-lr {
    margin: 0 1.3rem;
}
.text-all {
    border-bottom: 1px solid #F6F6F9;
    padding: .5rem 0;
    width: 100%;
    overflow: hidden;
}
.dis-box {
    display: -webkit-box;
    display: -moz-box;
    display: -ms-box;
    display: box;
}
.text-all label {
    font-size: 1.05rem;
    display: block;
    height: 3rem;
    line-height: 3rem;
    margin-right: 0.8rem;
    vertical-align: middle;
}
.input-text {
    position: relative;
}
.box-flex {
    -ms-box-flex: 1;
    box-flex: 1;
    display: block;
    width: 100%;
}
.input-text input {
    border: 0;
    height: 3rem;
    line-height: 2rem;
    padding: .5rem 0;
    box-sizing: border-box;
    width: 100%;
    color: #555;
    font-size: 1.05rem;
    padding-right: 3rem;
}
input:required, input:valid, input:invalid {
    border: 0 none;
    outline: 0 none;
    -ms-box-shadow: none;
    -o-box-shadow: none;
    box-shadow: none;
    -webkit-appearance: none;
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
}
.is-null {
    font-size: 2.1rem;
    color: #ddd;
    top: 50%;
    transition: all 0.2s;
    margin-top: -1.2rem;
    z-index: 10;
    position: absolute;
    right: 0.2rem;
    visibility: hidden;
    opacity: 0;
    transition: all 0.1s;
}
.icon-guanbi:before {
    content: "\e630";
}
.user-center .t-remark {
    margin-top: 1.2rem;
}
.t-remark, .t-remark:link, .t-remark a:link, .t-remark a {
    color: #777;
    font-size: 1.05rem;
}
.fr {
    float: right;
}
.user-center .btn-submit {
    margin-top: 1.2rem;
}
.btn-submit {
    background: #ff4146;
    border: 1px solid #ff4146;
}
.btn-submit, .btn-submit1, .btn-disab, .btn-cart, .btn-reset, .btn-default, .btn-alipay, .btn-wechat {
    font-size: 1.05rem;
    color: #fff;
    border: 0;
    text-align: center;
    padding: .64rem 0;
    border-radius: 4px;
    width: 100%;
}
.u-l-register {
    font-size: 1.05rem;
    text-align: center;
    margin-top: 2.2rem;
    display: block;
}
.icon-eye {
    display: block;
    width: 28px;
    height: 28px;
    background-image: url(../assets/img/eye.png);
    background-size: 100%;
    margin-bottom: 5px;
    position: absolute;
    top: 10px;
    right: 60px;
}



